<template>
    <div>
      <button @click="ifShow=!ifShow" class="btn btn-success">if-显示/隐藏{{ifShow}}</button>
      <div class="box" v-if="ifShow">
        我是div if
      </div>
      <button @click="show=!show" class="btn btn-success">show-显示/隐藏{{show}}</button>
      <div class="box" v-show="show">
        我是div show
      </div>
    </div>
</template>

<script>
    export default {
        data(){
            return{
              ifShow: true,
              show: true
          }
        }
    }
</script>

<style scoped>
  .box {
    width: 200px;
    height: 200px;
    background: blue;
  }
</style>



