<template>
  <div>
    <div class="panel panel-default">
      <div class="panel-heading">
        <h3 class="panel-title">Tolist ({{total}})</h3>
      </div>
      <div class="panel-body">
        <ul class="list-group">
          <li :class="{done:item.completed}"
              v-for="(item,index) in todos"
              class="list-group-item">{{item.title}} {{item.completed}}
            <button
              :class="item.completed?'btn-success':'btn-danger'"
              @click="toggle(index)"
              class="btn pull-right btn-xs">{{item.completed?'已完成':'完成'}}
            </button>
            <button class="btn btn-info btn-xs pull-right"
                    @click="del(index)">
              删除
            </button>
          </li>
        </ul>
        <input type="text" class="form-control" v-model="newData.title"/>
        <button class="btn btn-success" @click="addNews(newData)">add</button>
      </div>
      <div class="panel-footer"></div>
    </div>
  </div>
</template>

<script>
export default {
  data () {
    return {
      newData:{
        id:null,
        title:'',
        completed:false
      },
      todos: [{
        id: 1,
        title: '吃饭',
        completed:false
      },
        {
          id: 2,
          title: '睡觉',
          completed:false
        },
        {
          id: 3,
          title: '做梦',
          completed:false
        },
        {
          id: 4,
          title: '钱好多',
          completed:false
        },
        {
          id: 4,
          title: '想多了',
          completed:false
        }
      ]
      }
    }, methods: {
      addNews(data){
        this.todos.push(data);
        this.newData={
          id:null,
          title:''
        }
      },
      toggle(index){
        this.todos[index].completed=!this.todos[index].completed;
        console.log("haha")
      },
      del(index){
        this.todos.splice(index,1)
      }
    },
    computed:{
      total(){
        return this.todos.length;
      }
    }

}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
  .done{
    color: greenyellow;
    text-decoration: line-through;
  }
</style>



