<template>
    <div>
        <!--数据的显示 表达式和v-text指令-->
        {{message}}
        <span v-text="info"></span>
        <!--双向数据绑定 v-model-->
        <input type="text" v-model="message" />

        <hr />
        <!--循环数组显示数据-->
        <ul>
          <li v-for="(v,index) in lists">{{index}}---{{v.id}}---{{v.title}}=={{v.ob}}</li>
        </ul>
        <!--触发事件-->
        <button v-on:click="go()">点击</button>
        <button @click="run()">重击</button>
    </div>
</template>

<script>
    export default {
        data(){
            return{
              //数据
              message: '等会去不去跑步呢?',
              info: '我是vue',
              lists: [{
                id: 1,
                title: '去跑步',
                ob:'做喜欢的事'
              },
                {
                  id: 2,
                  title: '去健身',
                  ob:'做喜欢的事'
                },
                {
                  id: 3,
                  title: '去运动',
                  ob:'做喜欢的事'
                },
                {
                  id: 4,
                  title: '去快跑',
                  ob:'做喜欢的事'
                },
                {
                  id: 5,
                  title: '去俯卧撑',
                  ob:'做喜欢的事'
                }
              ]
            }
        },
      methods: {
        go() {
          console.log(" go go go")
        },
        run() {
          console.log("run run run")
        }
      }
    }
</script>

<style scoped>

</style>



