import Vue from 'vue'
import Router from 'vue-router'
import Hello from '@/components/Hello'
import yj from '@/components/yj'
import yjj from '@/components/yjj'
Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name: 'Hello',
      component: Hello
    },
    {
      path: '/yj',
      component:yj
    },
    {
      path: '/yjj',
      component:yjj
    }
  ]
})



